.projectBlock{
    width:250px;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    text-align:center;
    background-color:#7e7385;
    padding: 20px;
    box-sizing: border-box;
}

.imgContainer {
    width:100%;
    height:250px;
    display:flex;
    justify-content:center;
    align-items:center;
    background-color:#98949c;
    background-size:cover;
    background-position:center;
}

.button {
    background-color: white;
    color: black;
    border: 2px solid #008CBA;
    width:90px;
    height:30px;
}

.title {
    background-color:#b6bdd1;
}
.description{
    background-color:#b6bdd1;
}





