.contactsBlock {
    height:100vh;
    background-color:#ebbed9;
}
.contactsContainer {
    flex-direction:column;
    justify-content:space-between;
    display:flex;
    align-items:center;
}
.title {
        background-color:#bfbbbe;
}

.contactsForm {
    width:280px;
    height:150px;
    background-color:#a9d4eb;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    flex-wrap:wrap;
    border:1px solid;
}
.input {
       height:30px;
}
.textarea{
     height:60px;
}
.button {
    background-color: white;
    color: black;
    border: 2px solid #008CBA;
    width:90px;
    height:30px;
}